import {Injectable} from '@angular/core';
import {Image} from '../models/image';
import {from, Observable, ObservedValueOf} from 'rxjs';
import {FilesPaths} from '../models/files-paths';
import {Reply} from '../models/reply';
import {LocalStorageRepository} from '../repositories/local-storage.repository';
import {AngularFirestore, AngularFirestoreCollection} from '@angular/fire/compat/firestore';
import firebase from 'firebase/compat/app';
import DocumentReference = firebase.firestore.DocumentReference;
import {TimeHandler} from '../handlers/time.handler';
import {Grade} from '../models/grade';

@Injectable({providedIn: 'root'})
export class FireStoreService {
  private toSendCollection: AngularFirestoreCollection<Image>;
  toSend: Observable<Image[]>;
  private sentCollection: AngularFirestoreCollection<Image>;
  sent: Observable<Image[]>;

  constructor(private angularFirestore: AngularFirestore) {
    this.toSendCollection = angularFirestore.collection<Image>(FilesPaths.TOSEND);
    this.toSend = this.toSendCollection.valueChanges({idField: 'id'});
    this.sentCollection = angularFirestore.collection<Image>(FilesPaths.SENT);
    this.sent = this.sentCollection.valueChanges({idField: 'id'});
  }

  addImageToSend(image: Image): Observable<DocumentReference> {
    return from(this.toSendCollection.add(Object.assign({}, image)));
  }

  deleteImageToSend(image: Image): Observable<ObservedValueOf<Promise<void>>> {
    return from(this.toSendCollection.doc(image.id).delete());
  }

  addImageSent(image: Image): Observable<DocumentReference> {
    return from(this.sentCollection.add(Object.assign({}, image)));
  }

  deleteImageSent(image: Image): Observable<ObservedValueOf<Promise<void>>> {
    return from(this.sentCollection.doc(image.id).delete());
  }

  async moveImageFromToSendToSent(image: Image, newDrawable: string, newName: string): Promise<void> {
    await this.deleteImageSent(image).toPromise().then(
      () => {
        image.name = newName;
        image.drawable = newDrawable;
        image.date = TimeHandler.format();
        this.addImageSent(image).toPromise().then(() =>
          this.deleteImageToSend(image)
        );
      }
    ).catch(() => console.warn('firestore'));
  }

  async addReply(image: Image, text: string): Promise<void> {
    const reply: Reply = new Reply(this.angularFirestore.createId(), LocalStorageRepository.getDisplayName(), text);
    image.replies.push(reply);

    return await this.angularFirestore.doc(FilesPaths.SENT + '/' + image.id).update({
      replies: firebase.firestore.FieldValue.arrayUnion(...image.replies.map(nextReply => {
        return Object.assign({}, nextReply);
      }))
    });
  }

  async editReply(image: Image): Promise<void> {
    return await this.angularFirestore.doc(FilesPaths.SENT + '/' + image.id).update({
      replies: image.replies
    });
  }

  async deleteReply(image: Image, replyToDelete: Reply): Promise<void> {
    return await this.angularFirestore.doc(FilesPaths.SENT + '/' + image.id).update({
      replies: firebase.firestore.FieldValue.arrayRemove(replyToDelete)
    });
  }

  async addGrade(image: Image, value: number): Promise<void> {
    const grade: Grade = new Grade(this.angularFirestore.createId(), LocalStorageRepository.getDisplayName(), value);
    image.grades.push(grade);

    return await this.angularFirestore.doc(FilesPaths.SENT + '/' + image.id).update({
      grades: firebase.firestore.FieldValue.arrayUnion(...image.grades.map(nextReply => {
        return Object.assign({}, nextReply);
      }))
    });
  }

  async editGrade(image: Image): Promise<void> {
    return await this.angularFirestore.doc(FilesPaths.SENT + '/' + image.id).update({
      grades: image.grades
    });
  }

  async deleteGrade(image: Image, gradeToDelete: Grade): Promise<void> {
    return await this.angularFirestore.doc(FilesPaths.SENT + '/' + image.id).update({
      replies: firebase.firestore.FieldValue.arrayRemove(gradeToDelete)
    });
  }
}
