import {FilesPaths} from '../models/files-paths';
import {Observable} from 'rxjs';
import {Injectable} from '@angular/core';
import {AngularFireStorage} from '@angular/fire/compat/storage';
import {PictureNameHandler} from '../handlers/picture-name.handler';

@Injectable({providedIn: 'root'})
export class FireStorageService {
  constructor(private fireStorage: AngularFireStorage) {
  }


  uploadFileToSend(fileToUpload: File): { uploadProgress: Observable<number>; name: string; downloadUrl: Observable<string> } {
    const newName = new Date().getTime().toString() + '_' + fileToUpload.name;
    const filepath = FilesPaths.TOSEND + '/' + newName;
    const uploadTask = this.fireStorage.upload(filepath, fileToUpload);

    return {
      name: newName,
      uploadProgress: uploadTask.percentageChanges() as Observable<number>,
      downloadUrl: this.fireStorage.ref(filepath).getDownloadURL()
    };
  }

  async moveFileToSent(name: string, drawable: string): Promise<{ drawable: any; newName: string }> {
    const file = await fetch(drawable).then(
      data => data.blob()
    );
    const newName = PictureNameHandler.getName();
    const type = name.substr(name.length - 4);
    const newFilePath = `${FilesPaths.SENT}/${newName}${type}`;
    const moveTask = this.fireStorage.upload(newFilePath, file);

    await moveTask.then(() => this.fireStorage.refFromURL(drawable).delete());

    return {
      drawable: await this.fireStorage.ref(newFilePath).getDownloadURL().toPromise(),
      newName
    };
  }

  deleteFile(name: string): void {
    this.fireStorage.refFromURL(name).delete();
  }

}

