import {Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import {ImageRepository} from '../../repositories/image.repository';
import {MatDialog, MatDialogRef} from '@angular/material/dialog';
import {Observable} from 'rxjs';
import {SnackBar} from '../../handlers/snack-bar.handler';
import {delay} from 'rxjs/operators';

@Component({
  selector: 'app-upload',
  templateUrl: './upload.component.html',
  styleUrls: ['./upload.component.scss']
})
export class UploadComponent implements OnInit {
  @ViewChild('drop', {static: false}) elementReference: ElementRef<HTMLElement> = {} as ElementRef;
  @ViewChild('dropzone') dropzone: ElementRef<HTMLDivElement> = {} as ElementRef;
  files: any[] = [];
  startedUploading = false;


  constructor(
    private imageRepository: ImageRepository,
    private matDialog: MatDialog,
    private dialogRef: MatDialogRef<UploadComponent>,
    private snackbar: SnackBar
  ) {
  }

  ngOnInit(): void {
  }

  onFileDropped($event: any): void {
    this.prepareFilesList($event);
  }


  fileBrowseHandler(event: any): void {
    this.prepareFilesList(event.target.files);
  }

  deleteFile(index: number): void {
    this.files.splice(index, 1);
  }

  prepareFilesList(files: Array<any>): void {
    for (const item of files) {
      item.progress = 0;
      this.files.push(item);
    }
  }

  formatBytes(bytes: number, decimals = 2): string {
    if (bytes === 0) {
      return '0 Bytes';
    }
    const kilo = 1024;
    const decimal = decimals <= 0 ? 0 : decimals;
    const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
    const index = Math.floor(Math.log(bytes) / Math.log(kilo));
    return parseFloat((bytes / Math.pow(kilo, index)).toFixed(decimal)) + ' ' + sizes[index];
  }

  async upload(): Promise<void> {
    this.startedUploading = true;
    this.dialogRef.disableClose = true;

    const progresses: Observable<number>[] = [];

    this.files.forEach(file => {
      file.progress = this.imageRepository.upload(file);
      progresses.push(file.progress);
    });

    let counter = 0;

    for (const progress of progresses) {
      await progress.toPromise().then(() => counter++);
    }

    this.snackbar.open('Successfully uploaded all files!', 'Close');
    delay(1000);
    this.matDialog.closeAll();
  }

  onDrag(): any {
    if (this.dropzone !== null) {
      this.dropzone.nativeElement.style.backgroundColor = '#AAAAAA';
    }
  }

  onLeave(): any {
    if (this.dropzone !== null) {
      this.dropzone.nativeElement.style.backgroundColor = 'transparent';
    }
  }
}


