import {Component, Input, OnInit} from '@angular/core';
import {Reply} from '../../models/reply';
import {LocalStorageRepository} from '../../repositories/local-storage.repository';
import {ReplyRepository} from '../../repositories/reply.repository';
import {Image} from '../../models/image';
import {SnackBar} from '../../handlers/snack-bar.handler';

@Component({
  selector: 'app-reply-element',
  templateUrl: './reply-element.component.html',
  styleUrls: ['./reply-element.component.scss']
})
export class ReplyElementComponent implements OnInit {

  @Input()
  reply: Reply = new Reply('', '', '');
  @Input()
  image: Image = new Image('', '', '');

  username = LocalStorageRepository.getDisplayName();
  isEditing = false;

  constructor(
    private replyRepository: ReplyRepository,
    private snackBar: SnackBar
  ) {
  }

  ngOnInit(): void {
  }

  isAuthor(): boolean {
    return this.reply.author === this.username;
  }

  switchEditing(): void {
    this.isEditing = !this.isEditing;
  }

  deleteReply(): void {
    this.replyRepository.deleteReply(this.image, this.reply)
      .catch(
        () => this.snackBar.open('Something went wrong.', 'Close')
      );
  }

  editReply(): void {
    this.replyRepository.editReply(this.image)
      .then(
        () => this.isEditing = false
      )
      .catch(
        () => this.snackBar.open('Something went wrong.', 'Close')
      );
  }
}
