import {LocalStorageRepository} from '../repositories/local-storage.repository';

export class PictureNameHandler {
  static getName(): string {
    let day: number;
    let season: number;
    let name: string;

    if (LocalStorageRepository.getDay() === null) {
      LocalStorageRepository.setDay(dayOfYear().toString());
    }
    if (LocalStorageRepository.getSeason() === null) {
      LocalStorageRepository.setSeason(getSeason().toString());
    }

    season = parseInt(LocalStorageRepository.getSeason(), 10);
    day = parseInt(LocalStorageRepository.getDay(), 10);
    name = 'Season ' + season + ' Day ' + day;

    LocalStorageRepository.setDay((day + 1).toString());
    if (season % 4 === 0) {
      if (parseInt(LocalStorageRepository.getDay(), 10) === 366){
        LocalStorageRepository.setSeason((season + 1).toString());
      }
    }else if (parseInt(LocalStorageRepository.getDay(), 10) === 365){
      LocalStorageRepository.setSeason((season + 1).toString());
    }
    return name;
  }
}

function dayOfYear(): number {
  const now: any = new Date();
  const oneDay = 1000 * 60 * 60 * 24;
  const start: any = new Date(new Date().getFullYear(), 0, 0);

  const diff = (now - start) + ((start.getTimezoneOffset() - now.getTimezoneOffset()) * 60 * 1000);
  return Math.floor(diff / oneDay);
}

function getSeason(): number {
  const now: any = new Date();
  return now.getFullYear() - 2016;
}
