import {Component, Input, OnInit} from '@angular/core';
import {Image} from '../../models/image';
import {ImageRepository} from '../../repositories/image.repository';

@Component({
  selector: 'app-manager-sent-element',
  templateUrl: './manager-sent-element.component.html',
  styleUrls: ['./manager-sent-element.component.scss']
})
export class ManagerSentElementComponent implements OnInit {
  @Input()
  image: Image = new Image('', '', '');

  constructor(private imageRepository: ImageRepository) {
  }

  ngOnInit(): void {
  }

  deleteItem(): void {
    this.imageRepository.deleteFromSent(this.image);
  }

}
