import {Injectable} from '@angular/core';
import {FireStoreService} from '../services/fire-store.service';
import {Image} from '../models/image';
import {Grade} from '../models/grade';

@Injectable({providedIn: 'root'})
export class GradeRepository {
  constructor(
    private fireStoreService: FireStoreService
  ) {
  }

  async addGrade(image: Image, value: number): Promise<void> {
    return await this.fireStoreService.addGrade(image, value);
  }

  async editGrade(image: Image): Promise<void> {
    return await this.fireStoreService.editGrade(image);
  }

  async deleteGrade(image: Image, gradeToDelete: Grade): Promise<void> {
    return await this.fireStoreService.deleteGrade(image, gradeToDelete);
  }
}
