import {Component, Input, OnInit} from '@angular/core';
import {Image} from '../../models/image';
import {ImageRepository} from '../../repositories/image.repository';

@Component({
  selector: 'app-manager-to-send-element',
  templateUrl: './manager-to-send-element.component.html',
  styleUrls: ['./manager-to-send-element.component.scss']
})
export class ManagerToSendElementComponent implements OnInit {

  @Input()
  image: Image = new Image('', '', '');

  constructor(private imageRepository: ImageRepository) {
  }

  ngOnInit(): void {
  }

  deleteItem(): void {
    this.imageRepository.deleteFromToSend(this.image);
  }

}
