import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {StartComponent} from './components/start/start.component';
import {DashboardComponent} from './components/dashboard/dashboard.component';
import {Destinations} from './models/destinations';
import {AuthenticationGuard} from './handlers/authentication.guard';
import {ManagerComponent} from './components/manager/manager.component';

const routes: Routes = [
  {path: Destinations.EMPTY, redirectTo: Destinations.START, pathMatch: 'full'},
  {path: Destinations.START, component: StartComponent},
  {path: Destinations.DASHBOARD, component: DashboardComponent, canActivate: [AuthenticationGuard]},
  {path: Destinations.MANAGER, component: ManagerComponent, canActivate: [AuthenticationGuard]}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {
}
