import {Grade} from './grade';
import {Reply} from './reply';

export class Image {
  id?: string;
  name: string;
  drawable: string;
  date: string;
  doc?: string;
  replies: Reply[];
  grades: Grade[];

  constructor(name: string, drawable: string, date: string) {
    this.name = name;
    this.drawable = drawable;
    this.date = date;
    this.replies = [];
    this.grades = [];
  }
}
