import {FireStorageService} from '../services/fire-storage.service';
import {FireStoreService} from '../services/fire-store.service';
import {FileUploadMetadata} from '../models/file-upload-metadata';
import {Image} from '../models/image';
import {Observable} from 'rxjs';
import {Injectable} from '@angular/core';
import {RandomizeHandler} from '../handlers/randomize.handler';
import {PictureNameHandler} from '../handlers/picture-name.handler';
import {first} from 'rxjs/operators';
import {SnackBar} from '../handlers/snack-bar.handler';
import {TimeHandler} from '../handlers/time.handler';

@Injectable({providedIn: 'root'})
export class ImageRepository {
  constructor(
    private fireStorageService: FireStorageService,
    private fireStoreService: FireStoreService,
    private snackBar: SnackBar
  ) {
  }

  upload(file: File): Observable<number> {
    let image: Image;
    let url: string;
    const fileUploadMetadata: FileUploadMetadata = this.fireStorageService.uploadFileToSend(file);
    fileUploadMetadata.uploadProgress.toPromise().then(async progress => {
      await fileUploadMetadata.downloadUrl.toPromise().then(
        nextUrl => url = nextUrl
      );
      if (progress === 100) {
        image = new Image(fileUploadMetadata.name, url, TimeHandler.format());
        this.fireStoreService.addImageToSend(image);
      }
    });
    return fileUploadMetadata.uploadProgress;
  }

  deleteFromToSend(image: Image): void {
    this.fireStoreService.deleteImageToSend(image);
    this.fireStorageService.deleteFile(image.drawable);
  }

  deleteFromSent(image: Image): void {
    this.fireStoreService.deleteImageSent(image);
    this.fireStorageService.deleteFile(image.drawable);
  }

  async draw(): Promise<void> {
    this.snackBar.open('Drawing...', 'Close');
    await this.fireStoreService.toSend.pipe(first()).subscribe(
      async images => {
        let newDrawable: string;
        let newName: string;
        const image = RandomizeHandler.randomize(images);

        await this.fireStorageService.moveFileToSent(image.name, image.drawable).then(
          item => {
            newDrawable = item.drawable;
            newName = item.newName;

            this.fireStoreService.moveImageFromToSendToSent(image, newDrawable, newName);
            this.snackBar.open('Successfully drawn!', 'Close');
          }
        )
          .catch(() => this.snackBar.open('Failed to draw :C', 'Close'));
      }
    );
  }
}
