import {Component, Input, OnInit} from '@angular/core';
import {Grade} from '../../models/grade';
import {LocalStorageRepository} from '../../repositories/local-storage.repository';
import {GradeRepository} from '../../repositories/grade.repository';
import {Image} from '../../models/image';

@Component({
  selector: 'app-grade-bar',
  templateUrl: './grade-bar.component.html',
  styleUrls: ['./grade-bar.component.scss']
})
export class GradeBarComponent implements OnInit {
  @Input()
  grades: Grade[] = [];
  @Input()
  image: Image = new Image('', '', '');

  constructor(
    private gradeRepository: GradeRepository
  ) {
  }

  ngOnInit(): void {
  }

  onCLick(value: number): void {
    let action = false;
    console.log('started');
    this.grades.forEach(grade => {
      if (grade.value === value && grade.author === LocalStorageRepository.getDisplayName()) {
        console.log('deleting');
        action = true;
        this.gradeRepository.deleteGrade(this.image, grade);
      } else if (grade.value !== value && grade.author === LocalStorageRepository.getDisplayName()) {
        console.log('changing');
        action = true;
        grade.value = value;
        this.gradeRepository.editGrade(this.image);
      }
    });
    if (!action) {
      console.log('adding');
      this.gradeRepository.addGrade(this.image, value);
    }
  }

  countGrade(value: number): number {
    let counter = 0;
    this.grades.forEach(grade => {
      if (grade.value === value) {
        counter++;
      }
    });
    return counter;
  }

  hasUsersGradeButtonStyle(value: number): string {
    let attributes = 'mat-stroked-button';
    this.grades.forEach(grade => {
      if (grade.value === value && grade.author === LocalStorageRepository.getDisplayName()) {
        attributes = 'mat-raised-button';
      }
    });
    return attributes;
  }

  hasUserGradeColour(value: number): string {
    let colour = '';
    this.grades.forEach(grade => {
      if (grade.value === value && grade.author === LocalStorageRepository.getDisplayName()) {
        colour = 'primary';
      }
    });
    return colour;
  }
}
