import {FireStoreService} from '../services/fire-store.service';
import {Image} from '../models/image';
import {Reply} from '../models/reply';
import {Injectable} from '@angular/core';


@Injectable({providedIn: 'root'})
export class ReplyRepository {
  constructor(
    private fireStoreService: FireStoreService
  ) {
  }

  async addReply(image: Image, text: string): Promise<void> {
    return await this.fireStoreService.addReply(image, text);
  }

  async editReply(image: Image): Promise<void> {
    return await this.fireStoreService.editReply(image);
  }

  async deleteReply(image: Image, replyToDelete: Reply): Promise<void> {
    return await this.fireStoreService.deleteReply(image, replyToDelete);
  }
}
