import {Injectable} from '@angular/core';
import {SignUpRequest} from '../models/sign-up-request';
import {SignInRequest} from '../models/sign-in-request';
import {Router} from '@angular/router';
import {MatDialog} from '@angular/material/dialog';
import {SnackBar} from '../handlers/snack-bar.handler';
import {Destinations} from '../models/destinations';
import {LocalStorageRepository} from '../repositories/local-storage.repository';
import {AngularFireAuth} from '@angular/fire/compat/auth';

@Injectable({providedIn: 'root'})
export class AuthenticationService {
  constructor(
    private angularFireAuth: AngularFireAuth,
    private snackBar: SnackBar,
    private router: Router,
    private matDialog: MatDialog
  ) {
  }

  signUp(signUpRequest: SignUpRequest): void {
    this.angularFireAuth.createUserWithEmailAndPassword(signUpRequest.email, signUpRequest.password)
      .then(
        response => {
          response.user?.updateProfile({displayName: signUpRequest.displayName});
        }
      )
      .catch(
        error => {
          this.snackBar.open(error.message, 'Close');
        }
      );
  }

  signIn(signInRequest: SignInRequest): void {
    this.angularFireAuth.signInWithEmailAndPassword(signInRequest.email, signInRequest.password)
      .then(
        response => {
          LocalStorageRepository.setDisplayName(response.user?.displayName as string);
          LocalStorageRepository.setIsSignedIn('true');
          this.snackBar.open('Successfully signed in.', 'Close');
          this.router.navigate([Destinations.DASHBOARD]).then();
        }
      )
      .catch(
        error => {
          this.snackBar.open(error.message, 'Close');
        }
      );
  }

  signOut(): void {
    localStorage.clear();
    this.router.navigate([Destinations.START])
      .then(() => {
        LocalStorageRepository.setIsSignedIn('false');
        this.snackBar.open('User Signed out.', 'Close');
        this.matDialog.closeAll();
      });
  }

}
