import {LocalStorageKeys} from '../models/local-storage-keys';

export class LocalStorageRepository {

  static setDisplayName(value: string): void {
    localStorage.setItem(LocalStorageKeys.DISPLAY_NAME, value);
  }

  static getDisplayName(): string {
    return localStorage.getItem(LocalStorageKeys.DISPLAY_NAME) as string;
  }


  static setIsSignedIn(value: string): void {
    localStorage.setItem(LocalStorageKeys.IS_SIGNED_IN, value);
  }

  static getIsSignedIn(): string {
    return localStorage.getItem(LocalStorageKeys.IS_SIGNED_IN) as string;
  }

  static setDay(value: string): void {
    localStorage.setItem(LocalStorageKeys.DAY, value);
  }

  static getDay(): string {
    return localStorage.getItem(LocalStorageKeys.DAY) as string;
  }

  static setSeason(value: string): void {
    localStorage.setItem(LocalStorageKeys.SEASON, value);
  }

  static getSeason(): string {
    return localStorage.getItem(LocalStorageKeys.SEASON) as string;
  }
}
