import {Component, Input, OnInit} from '@angular/core';
import {Image} from '../../models/image';
import {FireStoreService} from '../../services/fire-store.service';
import {SnackBar} from '../../handlers/snack-bar.handler';
import {ReplyRepository} from '../../repositories/reply.repository';

@Component({
  selector: 'app-reply-create',
  templateUrl: './reply-create.component.html',
  styleUrls: ['./reply-create.component.scss']
})
export class ReplyCreateComponent implements OnInit {
  @Input()
  image: Image = new Image('', '', '');

  text = '';

  constructor(
    private replyRepository: ReplyRepository,
    private snackBar: SnackBar
  ) {
  }

  ngOnInit(): void {
  }

  addReply(): void {

    this.replyRepository.addReply(this.image, this.text)
      .then(
        () => this.text = ''
      ).catch(
      error => {
        this.snackBar.open('Something went wrong.', 'Close');
        console.log(error);
      }
    );
  }
}
